import ComponentError from '../helpers/component-error'
import resolveReference from '../helpers/component-reference'

const defaults = {
  openClass: '-open',
  expandCellClass: 'form-table-expand-trigger',
  expandIcon: 'fa-angle-right'
}

export default function ButtonGroup (reference, params) {
  params = {
    ...defaults,
    ...params,
    name: 'TableExpand'
  }

  const elements = resolveReference(reference, params.name)

  if (elements instanceof HTMLElement) {
    const [element] = elements
    return createInstance(element, params)
  }

  return elements
    .map(element => createInstance(element, params))
    .filter(element => element !== undefined)
}

function createInstance (table, params) {
  if (!(table instanceof HTMLTableElement)) {
    throw new ComponentError({
      name: params.name,
      message:
      'Erro ao criar a instância do componente.\nO elemento informado deve obrigatoriamente ser uma tabela (HTMLTableElement)',
      type: 'instace_table_not_found'
    })
  }

  const buttonInsert = getTableBtnInsert(table)
  const isReadOnly = !buttonInsert
  const rows = createExpandRows(table, isReadOnly, params)

  const instance = {
    table,
    rows,
    buttonInsert,
    isReadOnly,
    closeAll,
    openAll
  }

  addTableListners()

  table._tableExpand = instance

  return instance

  /**
   * 🔒 Private Methods
   */
  function addTableListners () {
    rows
      .forEach(row => addRowListners(row, instance.isReadOnly))

    if (!isReadOnly) {
      instance.buttonInsert
        .addEventListener('click', () => mountNewRow(isReadOnly))
    }
  }

  function addRowListners (row, isReadOnly) {
    row.open = open
    row.close = close

    row.expandElement
      .addEventListener('click', () => onClick(row))

    if (!isReadOnly) {
      row.deleteButton
        .addEventListener('click', () => removeRow(row))
    }
  }

  function mountNewRow (isReadOnly) {
    const lastRow = getLastRow(table)
    const rowInstance = getRowInstance(lastRow, params.expandCellClass)

    addRowListners(rowInstance)

    lastRow.classList.remove(params.openClass)
    instance.rows.push(rowInstance)
  }

  function removeRow (row) {
    const rowIndex = row.element.rowIndex

    // rowIndex it's only `1` when row isn't removed yet by
    // Orquestra `DeleteRow` function
    if (rowIndex === 1) {
      return
    }

    // the first table row is actually the table header
    instance.rows.splice(rowIndex - 1, 1)
  }

  function onClick (row) {
    row.element.classList.toggle(params.openClass)
    row.isOpen = !row.isOpen
  }

  /**
   * 🔑 Public Methods
   */
  function closeAll () {
    instance.rows
      .forEach(row => row.close())
  }

  function openAll () {
    instance.rows
      .forEach(row => row.open())
  }

  function close () {
    this.element.classList.remove(params.openClass)
    this.isOpen = false
  }

  function open () {
    this.element.classList.add(params.openClass)
    this.isOpen = true
  }
}

/**
 * 💡 Helper Functions
 */
function getTableBtnInsert (table) {
  return table.querySelector('#BtnInsertNewRow')
}

function getTableRows (table) {
  return [...table.querySelectorAll('tr:not(.header)')]
}

function getRowFirstCell (row, isReadOnly) {
  return isReadOnly
    ? row.querySelector('td:nth-child(1)')
    : row.querySelector('td:nth-child(2)')
}

function getfirstHeaderCell (table, isReadOnly) {
  return isReadOnly
    ? table.querySelector('tr.header td:nth-child(1)')
    : table.querySelector('tr.header td:nth-child(2)')
}

function getLastRow (table) {
  return table.querySelector('tr:last-child')
}

function getRowInstance (row, expandCellClass) {
  const expandElement = row
    .querySelector(`.${expandCellClass}`)

  const deleteButton = row
    .querySelector('.btn-delete-mv')

  return {
    expandElement,
    deleteButton,
    element: row,
    isOpen: false
  }
}

function createExpandRows (table, isReadOnly, params) {
  const rows = getTableRows(table)
  const firstHeaderCell = getfirstHeaderCell(table, isReadOnly)
  const emptyHeaderHtml = '<td class="form-table-expand-th"></td>'

  firstHeaderCell
    .insertAdjacentHTML('beforebegin', emptyHeaderHtml)

  return rows.map(row => {
    const firstCell = getRowFirstCell(row, isReadOnly)
    const expandCellHtml = getExpandCellMarkup(params)

    firstCell
      .insertAdjacentHTML('beforebegin', expandCellHtml)

    return getRowInstance(row, params.expandCellClass)
  })
}

function getExpandCellMarkup (params) {
  return `
  <td class="${params.expandCellClass}">
    <i class="form-table-expand-icon fas ${params.expandIcon}"></i>
  </td>`
}
