const config = {
  filters: {
    cnpj (value) {
      return value.replace(/(\d{2})(\d{3})(\d{3})(\d{4})(\d)/, '$1.$2.$3/$4-$5')
    },
    titleCase (value) {
      return value
        .toLowerCase()
        .split(' ')
        .map(word => {
          return word.charAt(0).toUpperCase() + word.slice(1)
        }).join(' ')
    }
  }
}

function setTriggers (field, data) {
  // Workaround para não observância do evento `change` em campos do tipo `data` do Orquestra.
  // Estes fazem uso do plugin `Bootstrap Datepicker`, o qual restringe o evento change nativo
  $(field).on('change', function (e) {
    handleChange(e, field, data)
  })
}

function handleChange (e, field, data) {
  const input = e.target
  const newValue = input.value || ''
  const id = field.getAttribute('xname').substring(3)
  const binds = document.querySelectorAll(`[data-bind=${id}]`)

  if (field.type === 'checkbox') {
    if (field.checked) {
      data[id].value.push(newValue)
    } else {
      data[id].value.splice(
        data[id].value.indexOf(newValue),
        1
      )
    }
    return handleUpdate(binds, data)
  }

  if (field.type === 'radio') {
    if (field.checked) {
      data[id].value = newValue
    }
    return handleUpdate(binds, data)
  }

  if (Array.isArray(data[id].value)) {
    const index = input.closest('tr').rowIndex - 1 // ignora a 1º linha usada como cabeçalho
    data[id].value[index] = newValue
  } else {
    data[id].value = newValue
  }

  return handleUpdate(binds, data)
}

function handleUpdate (binds, data) {
  binds.forEach(element => {
    update(element, data)
  })
}

const addField = {
  single (field, data) {
    const value = field.value || ''
    const defaultAttr = getDefaultAttributes(field)

    data[defaultAttr.id] = {
      value,
      ...defaultAttr
    }
  },

  collection (field, data) {
    const isInputMultiple = field.type === 'checkbox' || field.type === 'radio'
    const defaultAttr = getDefaultAttributes(field)

    if (!data[defaultAttr.id]) {
      data[defaultAttr.id] = {
        value: field.type === 'radio'
          ? null
          : [],
        ...defaultAttr
      }
    }

    if (isInputMultiple && !field.checked) {
      return
    }

    if (field.type === 'radio') {
      data[defaultAttr.id].value = field.value
      return
    }

    data[defaultAttr.id].value.push(
      field.value || ''
    )
  }
}

/**
 * Helpers
 */
const getIgnoredNotSelector = ids =>
  ids.map(id => `:not([xname=inp${id}])`)

const isFieldCollection = (field, context) => (
  context.querySelectorAll(`[xname=${field.getAttribute('xname')}]`).length > 1 ||
  context.querySelector(`[xname=${field.getAttribute('xname')}]`).type === 'checkbox' ||
  field.closest('table[mult=S]') !== null
)

const getDefaultAttributes = field => ({
  id: field.getAttribute('xname').substring(3),
  codfield: field.getAttribute('name').substring(3)
})

const getRowIndex = element => {
  return element.closest('tr').rowIndex - 1 // ignora a linha de cabeçalho
}

/**
 * Public API
 */
function init (
  { ignored = [], filters = {} } = { ignored: [], filters: {} },
  context = document
) {
  const data = {}
  const ignoredSelector = getIgnoredNotSelector(ignored)
  const fields = context.querySelectorAll(`[xname]${ignoredSelector.join()}`)

  document.data = data
  config.filters = { ...config.filters, ...filters }

  fields
    .forEach(field => {
      mount(field, data, context)
    })

  handleUpdate(
    document.querySelectorAll('[data-bind]'),
    data
  )

  document
    .querySelectorAll('[data-bind]')
    .forEach(element => update(element, data))
}

function mount (field, data = document.data, context = document) {
  field.dataset.id = field.getAttribute('xname').substring(3)

  setTriggers(field, data)

  isFieldCollection(field, context)
    ? addField.collection(field, data)
    : addField.single(field, data)
}

function update (element, data = document.data) {
  const fieldId = element.dataset.bind
  const filterName = element.dataset.filter

  if (!data[fieldId]) {
    return
  }

  const fieldValueIndex = element.dataset.index === '{row}'
    ? getRowIndex(element)
    : element.dataset.index || 0

  const fieldValue = Array.isArray(data[fieldId].value)
    ? data[fieldId].value[fieldValueIndex]
    : data[fieldId].value

  let fieldText = fieldValue

  if (element.hasAttribute('data-prefix')) {
    fieldText = fieldValue.substring(2)
  }

  if (filterName) {
    const filterFn = config.filters[filterName]

    if (!filterFn) {
      return console.error(`[Template] Filter function ${filterName} not found`)
    }

    fieldText = filterFn.call(config.filters, fieldText)
  }

  element.textContent = fieldText
}

function remove (field, data = document.data) {
  const fieldId = field.getAttribute('xname').substring(3)

  if (Array.isArray(data[fieldId].value)) {
    // previne a removação total de campos em tabelas multi-valoradas
    if (data[fieldId].value.length === 1) {
      return
    }

    const fieldValueIndex = getRowIndex(field)

    if (fieldValueIndex < 0) {
      console.error('[Template] You are trying to remove a field from data that already been deleted from DOM')
    }

    data[fieldId].value.splice(fieldValueIndex, 1)
  } else {
    delete data[fieldId]
  }
}

export default {
  init,
  mount,
  update,
  remove
}
