import config from './equipments-config'
import EquipmentsApi from './api/api.equipments'
import { FormValidation } from '../../components/form-validation'

const SearchForm = new FormValidation([
  {
    element: '[xname=inpunidadeSesi]',
    errorMessage: 'Informe uma Unidade para pesquisar'
  }, {
    element: '[xname=inpsetorEquipamento]',
    errorMessage: 'Informe o setor dos equipamentos'
  }, {
    element: '[xname=inpperiodoDeReferencia]',
    errorMessage: 'Informe um período'
  }
])

let AppEvents

function handleSearch (e) {
  const validationResult = SearchForm.validate()
  const params = {
    unitId: config.form.$unity.value,
    period: $(config.form.$period).datepicker('getDate'),
    equipmentSector: config.form.$sector.value,
    equipmentGroup: config.form.$group.value
  }

  e.preventDefault()

  if (validationResult.hasErrors) {
    return
  }

  addLoadingState()
  searchEquipments(params)
}

function setReferenceTermNeeded () {
  const { processVariables } = config
  const sectorId = this.value
  const securitySectorsIds = ['04', '05']

  if (securitySectorsIds.includes(sectorId)) {
    processVariables.$referenceTermNeeded.value = 'Não'
  } else {
    processVariables.$referenceTermNeeded.value = 'Sim'
  }
}

function searchEquipments (params) {
  EquipmentsApi
    .getDataFromDataSource(params)
    .then(data => mountTable(data))
    .catch(err => handleSearchError(err))
}

function mountTable (data) {
  config.$emptyMessage
    .classList.add('is-hidden')

  removeLoadingState()

  AppEvents.emit('search-done', data)
}

function handleSearchError (err) {
  console.error(err)
  removeLoadingState()
}

function addLoadingState () {
  config.form.$btnSearch
    .classList
    .add('-loading')

  config.$pane
    .classList
    .add('-loading')

  config.$table
    .classList
    .add('is-hidden')
}

function removeLoadingState () {
  config.form.$btnSearch
    .classList
    .remove('-loading')

  config.$pane
    .classList
    .remove('-loading')
}

/**
 * Public API
 */
function mount (EventEmitter) {
  AppEvents = EventEmitter

  config.form.$btnSearch
    .addEventListener('click', handleSearch)

  config.form.$sector
    .addEventListener('change', setReferenceTermNeeded)

  $(config.form.$period)
    .datepicker({
      language: 'pt-BR',
      startView: 'months',
      minViewMode: 'months',
      format: 'M/yyyy'
    })
    .attr('autocomplete', 'off')
}

export default {
  mount
}
