
import Emitter from 'tiny-emitter'
import Template from './components/template'
import Form from './components/form'
import EquipmentsTable from './modules/equipments/equipments-table'
import EquipmentsForm from './modules/equipments/equipments-form'
import InterventionForm from './modules/intervention/intervention-form'

const AppEvents = new Emitter()

Template.init()
Form.conditionals()

Form.on('avulso', function () {
  EquipmentsForm.mount(AppEvents)

  AppEvents
    .on('search-done', function (tableData) {
      EquipmentsTable.mount(tableData)
    })
})

Form.on('controle', function () {
  EquipmentsTable.mount()
})

Form.on(['checklist-certificado', 'checklist-relatorio'], function (taskAlias) {
  InterventionForm.mount(taskAlias)
})
