import config from './equipments-config'
import TableExapnd from '../../components/table-expand'
import { appendData } from '../../components/table-multivalue'
import Template from '../../components/template'

function addTagsLocationVariant (table, fieldId) {
  table
    .querySelectorAll('tr:not(.header')
    .forEach(tr => {
      const tag = tr.querySelector(`[data-bind="${fieldId}"]`)
      const variant = getTagVariant(tag.textContent)

      tag.classList.add(variant)
    })
}

function getTagVariant (value) {
  if (value === 'Interno') {
    return '-yellow'
  }
  if (value === 'Externo') {
    return '-blue'
  }

  return null
}

/**
 * Public API
 */
function mount (tableData) {
  const tagFieldId = 'metodoCalibracao'
  const equipmentsTable = TableExapnd(config.$table)

  if (tableData) {
    appendData(
      config.$table,
      tableData
    )
  }

  Template.init()

  addTagsLocationVariant(config.$table, tagFieldId)

  config.$table
    .classList.remove('is-hidden')

  return equipmentsTable
}

export default {
  mount
}
