const ORQUESTRA_DATASOURCE_URL = process.env.DATASOURCE_BASE_URL + process.env.DATASOURCE_EQUIPMENTS

const getDataFromDataSource = function ({
  unitId,
  period,
  equipmentSector,
  equipmentGroup
}) {
  const url = new URL(ORQUESTRA_DATASOURCE_URL)

  const params = {
    inppunidadeSesi: unitId,
    inpperiodoDeReferencia: `${period.getMonth() + 1}/${period.getFullYear()}`,
    inppequipmentSector: equipmentSector,
    inpgrupoDeEquipamento: equipmentGroup
  }

  url.search = new URLSearchParams(params).toString()

  return fetch(url)
    .then(res => res.json())
    .then(({ success }) => {
      return success.map(({ fields }) => fields)
    })
}

export default {
  getDataFromDataSource
}
