import Form from '../../components/form'
import ButtonRadio from '../../components/button-radio'
import InputRadio from '../../components/input-radio'

function addCertificateFormHandlers () {
  Form.handler({
    target: 'calibracaoSegundoNormaTecnica',
    events: 'change'
  }, function (value) {
    if (value === '1. Sim') {
      Form.showField('normaTecnica')
    } else {
      Form.hideField('normaTecnica')
    }
  })

  Form.handler({
    target: 'laboratorioCalibracaoRbc',
    events: 'change'
  }, function (value) {
    const fields = [
      'certificadosDosPadroesFornecidos',
      'padoresSecundariosCalibradosRbc',
      'calibracaoDosPadroesSecundariosNaValidade'
    ]

    if (value === '2. Não') {
      fields.forEach(field => Form.showField(field))
    } else {
      fields.forEach(field => Form.hideField(field))
    }
  })

  Form.handler({
    target: 'equipamentoAptoUtilizacao',
    events: 'change'
  }, function (value) {
    if (value === '2. Não') {
      Form.showField('destinoEquipamento')
    } else {
      Form.hideField('destinoEquipamento')
    }
  })
}

function addReportFormHandler () {
  Form.handler({
    target: 'equipamentoAprovadoPeloFornecedor',
    events: 'change'
  }, function (value) {
    if (value === '2. Não') {
      Form.showField('destinoEquipamento')
    } else {
      Form.hideField('destinoEquipamento')
    }
  })
}

function handlePreviousAndNextIntervention () {
  const $lastDate = document.querySelector('[xname=inpultimaCalibracao]')
  const $nextDate = document.querySelector('[xname=inpproximaCalibracaoPrevisto]')

  const $targets = [
    $nextDate,
    document.querySelector('[name=proximaCalibracaoPrevisto]')
  ]

  // Limpa os campos com as datas da última e próxima calibração
  // para que sejam validados pelo Orquestra como campos obrigatórios
  // caso a tarefa seja concluída sem o preenchimento do campo `ultimaCalibracao`
  $lastDate.value = ''
  $nextDate.value = ''

  $('[name=ultimaCalibracao]')
    .on('change', function () {
      calculateNextIntervention($(this), $lastDate, $targets)
    })
}

function calculateNextIntervention ($input, $origin, $targets) {
  if ($input.val() === '') {
    return [...$targets, $origin].forEach($input => {
      $input.value = ''
    })
  }

  const lastDate = $input.datepicker('getDate')
  const nextDate = new Date(lastDate)
  const daysToAdd = parseInt(document.querySelector('[xname=inpqtdDiasFrequenciaCalibracao]').value)

  nextDate.setDate(nextDate.getDate() + daysToAdd)

  $origin.value = dateToStringDate(lastDate)

  $targets.forEach($target => {
    $target.value = dateToStringDate(nextDate)
  })
}

function dateToStringDate (date) {
  return date.toLocaleString('pt-BR', { year: 'numeric', month: 'numeric', day: 'numeric' })
}

/**
 * Public API
 */
function mount (taskAlias) {
  ButtonRadio('[data-btn-radio]', { hidePrefix: true })
  InputRadio('[data-input-radio]', { hidePrefix: true })

  handlePreviousAndNextIntervention()

  if (taskAlias === 'checklist-certificado') {
    addCertificateFormHandlers()
  }

  if (taskAlias === 'checklist-relatorio') {
    addReportFormHandler()
  }
}

export default {
  mount
}
