function registerEvents (tableInstance) {
  tableInstance.btnInsert.onclick = ''
  tableInstance.btnInsert.removeEventListener('click', InsertNewRow)
  tableInstance.btnInsert.addEventListener('click', function (e) {
    handeClick.call(this, e, tableInstance)
  })

  tableInstance
    .rows()
    .forEach(row => registerRowEvents(tableInstance, row))
}

function registerRowEvents (tableInstance, row) {
  const btnDelete = row.querySelector('.btn-delete-mv')

  btnDelete.onclick = ''
  btnDelete.removeEventListener('click', DeleteRow)
  btnDelete.addEventListener('click', function (e) {
    handleRemove.call(this, e, tableInstance)
  })
}

function getEventConfig (tableInstance) {
  return {
    bubbles: true,
    detail: {
      table: tableInstance.table,
      length: getTableRowsLength(tableInstance.table),
      lastRow: getLastRow(tableInstance.table)
    }
  }
}

function handeClick (e, tableInstance) {
  this.dispatchEvent(
    new CustomEvent('beforeInsert', getEventConfig(tableInstance))
  )

  InsertNewRow(this, true)

  const newestRow = getLastRow(tableInstance.table)

  registerRowEvents(tableInstance, newestRow)

  if (tableInstance.onMount) {
    tableInstance.onMount(newestRow)
  }

  this.dispatchEvent(
    new CustomEvent('afterInsert', getEventConfig(tableInstance))
  )
}

function handleRemove (e, tableInstance) {
  const eventConfig = getEventConfig(tableInstance)
  eventConfig.detail.deletedRow = this.closest('tr')

  // chamando o evento diretamente na <table>
  tableInstance.table.dispatchEvent(
    new CustomEvent('beforeDelete', eventConfig)
  )

  DeleteRow(this)

  tableInstance.table.dispatchEvent(
    new CustomEvent('afterDelete', eventConfig)
  )
}

function removeRows (table) {
  const btnInsert = table.querySelector('#BtnInsertNewRow')

  btnInsert.click()
  table
    .querySelectorAll('tr:not(.header):not(:last-child')
    .forEach(row => row.remove())
}

/**
 * Helpers
 */
const getRows = table =>
  table.querySelectorAll('tr:not(.header)')

const getLastRow = table =>
  table.querySelector('tr:last-child')

const getTableRowsLength = table =>
  getRows(table).length

/**
 * Public Constructor
 */
export class TableMultivalue {
  constructor (tableElementSelector, onMount, context = document) {
    this.table = context.querySelector(tableElementSelector)
    this.onMount = onMount

    if (!this.table) {
      console.error(`[Table Multivalue] table not found for the following selector: ${tableElementSelector}`)
    }

    if (this.onMount) {
      this.table
        .querySelectorAll('tr:not(.header)')
        .forEach(row => this.onMount(row))
    }

    this.btnInsert = this.table.querySelector('#BtnInsertNewRow')
    registerEvents(this)
  }

  static () {
    this.table.classList.add('-static')
  }

  active () {
    this.table.classList.remove('-static')
  }

  reset () {
    removeRows(this.table)
  }

  on (eventName, callback) {
    this.table.addEventListener(eventName, callback)
  }

  rows () {
    return getRows(this.table)
  }

  lastRow () {
    return getLastRow(this.table)
  }

  length () {
    return getTableRowsLength(this.table)
  }
}

/**
 * @todo implementar usando parâmetros também...
 * colocar as funções auxiliares dentro de um namespace
 */
export function mountTables () {
  document.querySelectorAll('[mult=S]')
    .forEach(table => {
      const isActive = table.querySelector('#BtnInsertNewRow')

      if (!isActive) {
        table.classList.add('-readonly')
      }
    })
}

export function appendData (table, data, addFirstRow = false) {
  removeRows(table)

  data.forEach((field, rowIndex) => {
    if (addFirstRow || rowIndex > 0) {
      table.querySelector('#BtnInsertNewRow').click()
    }

    Object.entries(field).forEach(([id, value]) => {
      const lastRow = getLastRow(table)
      const field = lastRow.querySelector(`[xname=inp${id}]`)

      if (field) {
        field.value = value
      }
    })
  })
}
