import ComponentError from '../helpers/component-error'
import resolveReference from '../helpers/component-reference'
import { removeOptionPrefix } from '../helpers/component-helpers'

const defaults = {
  radioInnerClass: 'input-radio-inner',
  radioHelpClass: 'ctrl-option-help',
  activeClass: '-active',
  hidePrefix: false,
  splitSeparator: '.'
}

export default function InputRadio (reference, params) {
  params = {
    ...defaults,
    ...params,
    name: 'InputRadio'
  }

  const elements = resolveReference(reference, params.name)

  if (elements instanceof HTMLElement) {
    const [element] = elements
    return createInstance(element, params)
  }

  return elements
    .map(element => createInstance(element, params))
    .filter(element => element !== undefined)
}

function createInstance (element, params) {
  const {
    radios,
    textvalue
  } = getOrquestraField(element, params)

  const checked = radios
    .find(radio => radio.checked)

  if (textvalue && params.hidePrefix) {
    removeTextPrefix(textvalue)
  }

  if (!radios) {
    return
  }

  createInputRadio(radios, element, params)

  const state = {
    value: '',
    isEnable: true
  }

  const instance = {
    state,
    element,
    radios,
    checked,
    reset,
    set,
    get
  }

  addListners(radios)

  if (checked) {
    set(checked.value)
  }

  element._inputRadio = instance

  return instance

  /**
   * 🔒 Private Methods
   */
  function addListners (radios) {
    radios
      .forEach(radio => {
        radio.addEventListener('click', onClick)
      })
  }

  function onClick () {
    instance.checked = this
    state.value = this.value
  }

  function removeTextPrefix (textvalue) {
    const value = removeOptionPrefix(textvalue.textContent, params.splitSeparator)
    textvalue.textContent = value
  }

  /**
   * 🔑 Public Methods
   * @todo Destroy / AddOption / RemoveOption / Disable (readonly) / Enable / TextOnlys
   */
  function reset () {
    state.value = ''
    instance.radios
      .forEach(radio => {
        radio.checked = false
      })
  }

  function set (option) {
    const radio = instance.radios
      .find(radio => radio.value === option)

    if (!radio) {
      throw new ComponentError({
        name: params.name,
        message:
        `Erro ao realizar set para novo valor.\nNão foi encontrada nenhuma opção para o valor ${option}`,
        type: 'option_not_found'
      })
    }

    onClick.apply(radio)
  }

  function get () {
    return state.value
  }
}

/**
 * 💡 Helper Functions
 */

function getOrquestraField (element, params) {
  const radios = [...element.querySelectorAll('input[type=radio][xname]')]
  const textvalue = element.querySelector('div[xid]')

  if (!radios && !textvalue) {
    throw new ComponentError({
      name: params.name,
      message:
      'Erro ao criar a instância do componente.\nNenhum campo do tipo múltipla seleção (radio) Orquestra encontrado',
      type: 'instace_field_not_found'
    })
  }

  return {
    radios,
    textvalue
  }
}

function createInputRadio (radios, element, params) {
  const radioInnerHtml = getRadioInnerMarkup(params.radioInnerClass)

  radios.forEach(radio => {
    const radioOption = radio.value
    const radioHelper = getRadioHelper(element, radioOption, params)
    const textNode = radio.nextSibling
    const radioText = params.hidePrefix
      ? removeOptionPrefix(radioOption, params.splitSeparator)
      : textNode.textContent

    textNode.remove()

    if (radioHelper) {
      radio.insertAdjacentElement('afterend', radioHelper)
    }

    radio.insertAdjacentHTML('afterend', `<span>${radioText}</span>`)
    radio.insertAdjacentHTML('afterend', radioInnerHtml)
  })
}

function getRadioHelper (element, radioOption, params) {
  const radioHelper = element.querySelector(`[data-ctrl-help="${radioOption}"]`)

  if (!radioHelper) {
    return null
  }

  const radioHelperClone = radioHelper.cloneNode(true)
  delete radioHelperClone.dataset.ctrlHelp
  radioHelperClone.classList.add(params.radioHelpClass)

  return radioHelperClone
}

function getRadioInnerMarkup (radioInnerClass) {
  return `<div class="${radioInnerClass}"></div>`
}
